<template>
  <div class="preloader">
    <div class="preloader__loader">
      <img :src="logo" class="preloader__logo" />
    </div>
  </div>
</template>
<script>
import logo from "../assets/logo.png";
export default {
  data() {
    return {
      logo: logo
    };
  },
  methods: window.addEventListener("load", function() {
    const loader = document.querySelector(".preloader");
    loader.className += " hidden";
  })
};
</script>
<style lang="scss" scoped>
@import "../assets/css/_variable.scss";
@import "../assets/css/_global.scss";
@import "../assets/css/_grid.scss";
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
  width: 100vw;
  background-color: #fff;
  z-index: 9999;
  &__loader {
    // position: absolute;
    // top: 46%;
    // left: 46%;
    // transform: translate(-50%, -50%);
    margin: auto;
    width: 100px;
    height: 100px;
    border: 2px solid;
    border-radius: 50%;
    border-top-color: #cecece;
    animation: spin 0.8s infinite;
    display: flex;
    flex-wrap: wrap;
    @media only screen and (max-width: $smartphone) {
      left: 50%;
    }
  }
  &__logo {
    width: 50px;
    margin: auto;
  }
}
.hidden {
  animation: fadeOut 1s;
  animation-fill-mode: forwards;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes fadeOut {
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
</style>
