<template>
  <section class="section1">
    <Me></Me>
    <About></About>
    <div class="title-header">My Work</div>
    <Work></Work>
    <!-- <div v-for="(infoss, index) in infoss" :key="index">{{ infoss.name }}</div> -->
  </section>
</template>
<script>
import logo from "@/assets/logo.png";
import Work from "@/components/Content_Grid_All.vue";
import Me from "@/components/Me.vue";
import About from "@/components/About.vue";
import axios from "axios";
export default {
  data() {
    return {
      logo: logo,
      infoss: null
    };
  },
  components: {
    Me,
    Work,
    About
  },
  mounted() {
    axios
      .get("https://pokeapi.co/api/v2/ability/")
      .then(response => (this.infoss = response.data.results));
  }
};
</script>
