import Vue from "vue";
import App from "./main.vue";
import router from "./router";
import store from "./store";
import "./registerServiceWorker";
import VModal from 'vue-js-modal'

Vue.config.productionTip = false;
Vue.use(VModal)
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
